/* Media Queries */
@media screen and (min-width: 0px) {
  /* Header */
  header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 6em auto;
  }
  header .Aloha-logo {
    grid-column: 1;
  }
  header nav {
    grid-column: 1/3;
    grid-row: 2;
  }
  header .Cart-logo {
    grid-column: 2;
  }

  /* Categories */
  .categories {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-gap: 1em;
  }
  .categories .mens {
    grid-column: 1/3;
    grid-row: 1;
  }
  .categories .trends {
    grid-column: 1;
    grid-row: 2;
  }
  .categories .glasses {
    grid-column: 2;
    grid-row: 2;
  }
  .categories .sale {
    grid-column: 1;
    grid-row: 3;
  }
  .categories .bags {
    grid-column: 2;
    grid-row: 3;
  }
  .categories .womens {
    grid-column: 1/3;
    grid-row: 4;
  }

  /* Updates */
  .updates {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .updates h1 {
    grid-row: 1;
    grid-column: 1/4;
  }
  .updates p {
    grid-row: 2;
    grid-column: 1/4;
  }
  .updates form {
    grid-row: 3;
    grid-column: 1/4;
  }
}
