    background-position: center;
  }
  .categories .womens {
    background-size: cover;
    background-repeat: no-repeat;
  }

  /* Most loved products */
  .mostlovedproducts .carousel-cell {
    width: 100%;
  }

  /* Footer */
  footer {
    padding-bottom: 2.5rem;
    text-align: center;
  }
  footer .details {
    justify-self: center;
    line-height: 1.25rem;
    margin-top: 2.5rem;
  }
  footer .details .address {
    margin-top: 1.25rem;
  }
  footer .logo {
    display: none;
  }
  footer .socialmedia ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  footer .socialmedia ul li {
    padding-top: 1.5rem;
  }
  footer .socialmedia ul li a i {
    min-width: 25px;
  }
  footer .socialmedia ul li a span {
    display: none;
  }
}
